.app{
    
    height: 100vh;


    .sections{

        width: 100%;
        height: calc(100vh - 140px);
        background-color: purple;
        position: relative;
        top: 70px;
        
        scroll-behavior: smooth;
        scroll-snap-type: y mandatory;
        scrollbar-width: none; //for fire fox
        
        &::-webkit-scrollbar{
            display: none;
        }
        >*{

            width: 100vm;
            height: calc(100vh - 140px);
            scroll-snap-align: start;
        }
        
        
    }

}
