
.menu{
    width: 300px;
    height: 100vh;
    background-color: orangered;
    position:fixed;
    top: 0;
    right: -300px;
    z-index: 2;
    display: flex;
    align-items: center;
    justify-content: center;
    // row
    flex-direction: column;
    text-decoration: none;
    transition: all 1s ease;
    
    &.active{
        right: 0;
    }

    ul{
        margin :0;
        padding: 0;
        list-style: none;
        font-size: 40px;
        font-weight: 300;
        width: 80%;

        li{
            margin-bottom: 25px;
            a{
                font-size: inherit;
                color: inherit;
                text-decoration: none;
            }

            &:hover {
                font-weight: 600;
            }
        }


    }

}